<template>
  <div class="cart">
    <!-- 导航 -->
    <nav-bar class="home-nav">
      <template v-slot:center>
        <div>购物车（{{ length }}）</div>
      </template>
    </nav-bar>

    <!-- 商品的列表 -->
    <cart-list></cart-list>

    <!-- 底部汇总 -->
    <cart-bottom-bar></cart-bottom-bar>
  </div>
</template>

<script>
import NavBar from "components/common/navbar/NavBar";

import CartList from "./childComps/CartList";
import CartBottomBar from "./childComps/CartBottomBar";

import { mapGetters } from "vuex"; //辅助函数，将store中的getters组件映射到局部计算属性

export default {
  name: "Cart",
  components: {
    NavBar,
    CartList,
    CartBottomBar,
  },
  computed: {
    // 两种语法
    // 1.正常语法
    // ...mapGetters(['cartLength','cartList'])
    // 2.如果你想将一个getter属性另取一个名字，使用对象形式
    ...mapGetters({
      length: "cartLength",
    }),
  },
};
</script>

<style scoped>
.cart {
  height: 100vh;
}
.home-nav {
  background-color: deeppink;
  color: #fff;
}
</style>
