<template>
  <div class="bottom-bar">
    <div class="check-content">
      <check-button
        class="check-button"
        :is-checked="isSelectAll"
        @click.native="checkClick"
      ></check-button>
      <span>全选</span>
    </div>

    <div class="price">合计：{{ totalPrice }}</div>

    <div class="calculate" @click="calcClick">去计算({{ checkLength }})</div>
  </div>
</template>

<script>
import CheckButton from "./CheckButton";

import { mapGetters } from "vuex";

export default {
  name: "CartBottomBar",
  components: {
    CheckButton,
  },
  computed: {
    ...mapGetters(["cartList"]),
    totalPrice() {
      return (
        "￥" +
        this.cartList
          .filter((item) => {
            return item.checked;
          })
          .reduce((preValue, item) => {
            return preValue + item.price * item.count;
          }, 0)
          .toFixed(2)
      );
    },
    checkLength() {
      return this.cartList.filter((item) => {
        return item.checked;
      }).length;
    },
    isSelectAll() {
      // 1.使用filter：
      // if (this.cartList.length === 0) return false;
      // return !this.cartList.filter((item) => !item.checked).length;
      // 2.使用find：
      // if (this.cartList.length === 0) return false;
      // return !this.cartList.find((item) => !item.checked);
      // 3.普通遍历
      if (this.cartList.length === 0) return false;
      for (let item of this.cartList) {
        if (!item.checked) {
          return false;
        }
      }
      return true;
    },
  },
  methods: {
    checkClick() {
      if (this.isSelectAll) {
        //如果全部选中
        this.cartList.forEach((item) => (item.checked = false));
      } else {
        //部分或全部不选中
        this.cartList.forEach((item) => (item.checked = true));
      }
    },
    calcClick() {
      if (!this.isSelectAll) {
        this.$toast.show('请选择购买的商品',2000)
      }
    },
  },
};
</script>

<style scoped>
.bottom-bar {
  display: flex;
  position: relative;

  height: 40px;
  line-height: 40px;

  background-color: #eee;
}
.check-content {
  display: flex;
  align-items: center;
  width: 60px;
}
.check-button {
  width: 22px;
  height: 22px;
  line-height: 22px;
}
.check-content span {
  margin-left: 5px;
}
.price {
  margin-left: 24px;
  flex: 1;
}
.calculate {
  width: 90px;
  background-color: red;
  text-align: center;
}
</style>